import React from 'react';

import '../App.css';

import Filter from '../components/filter';
import Map from '../components/map';
import List from '../components/list';

class Home extends React.PureComponent {
    constructor(props) {
        super(props);
        
        this.state = {
            no_bed: 1,
            no_bath: 1,
            no_toilets: 1,
    
            tno_bed: 1,
            tno_bath: 1,
            tno_toilets: 1,
    
            currentArea: {
                lat: 6.5005,
                lng: 3.3666
            },
            prices: [],
            areaPrice: 0,
            mode: true,
            sort: 'high',
        }
    }

    componentDidMount () {    
        this.setState({
            areas: majorAreasData()
        }, () => {
            this.getAddressRange();
            this.getAreasRange();
        });
    
        $(window).on('scroll', function () { 
          $(".header").css("top", $(this).scrollTop() * .5);
        });
    }
    
    render () {
        return (
            <>
                <section>
                    <div className="container">
                        <Map />
                        <Filter showSort={true} />
                        <List />
                    </div>
                </section>
            </>
        );
    }   
}

export default Home;